@import '../../assets/styles/main.scss';

#xp {
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;

  &::before{
    content: '';
    position: absolute;
    top: 0;
    height: 100%;
    background-color: $secundary-text-color;
    background: linear-gradient(180deg, rgba($secundary-text-color,0) 0%, rgba($secundary-text-color,1) 15%, rgba($secundary-text-color,1) 85%, rgba($secundary-text-color,0) 100%);
  }

  @media (min-width: $small-width){
    padding: 100svh $small-secundary-padding;
    grid-gap: 100svh;

    &::before{
      width: 3px;
      left: calc($large-secundary-padding/2);
    }
  }
  
  @media (min-width: $medium-width){
    padding: 70vh $medium-secundary-padding;
    grid-gap: 100svh;

    &::before{
      width: 2px;
      left: calc($large-secundary-padding/1.5);
    }
  }
  
  @media (min-width: $large-width){
    padding: 200svh $large-secundary-padding;
    grid-gap: 100svh;

    &::before{
      width: 2px;
      left: calc($large-secundary-padding/1.5);
    }

  }

}


